/* :root {
    --width: 1000px;
    --height: 650px;
  } */

  #main {
    position: relative;
    /* margin-top: 10px; */
    display: flex;
    justify-content: center;
    width: 1000px;
    margin: 20px auto;
    width: var(--width);
    height: var(--height);
    font-size: 32px;
    font-family: Arial, Helvetica, sans-serif;
  }

  canvas {
    /* position: absolute; */
    left: 0;
    top: 0;
    z-index: 10;
    border-top: 1px solid grey;
    border-left: 1px solid grey;
    border-right: 1px solid grey;
    margin-bottom: 40px;
    background-image: url("./images/background.png");
  }

  #display {
    /* position: absolute; */
    bottom: 0;
    /* display: flex; */
    /* justify-content: space-between; */
    text-align: center;
    width: var(--width);
  }

  #score,
  #info {
    width: 200px;
  }